.iron-image-container{position:relative;min-width:100%;min-height:100%;overflow:hidden}.iron-image-preload{position:absolute;z-index:-2;top:0;right:0;bottom:0;left:0;min-width:100%;min-height:100%;background-size:cover;background-repeat:no-repeat;background-position:center center;-webkit-filter:blur(4px);filter:blur(4px)}.iron-image-loaded{position:absolute;z-index:-1;top:0;right:0;bottom:0;left:0;min-width:100%;min-height:100%;background-size:cover;background-repeat:no-repeat;background-position:center center;opacity:0;-webkit-transition:opacity 3s ease;-o-transition:opacity 3s ease;transition:opacity 3s ease}.iron-image-fade-in{opacity:1}



